<template>
  <div>
    <fTitle>
      <template #title>DatePicker 日期选择器</template>
      <template #introduce>用于选择或输入日期</template>
    </fTitle>
    <sTitle>
      <template #title>选择日</template>
      <template #introduce>以「日」为基本单位，基础的日期选择控件</template>
    </sTitle>
    <div class="outbox">
      <div class="inbox">
        <el-row type="flex" justify="space-around">
          <el-col :span="8"> <div class="grid-content">
            <H3>默认</H3>
    <el-date-picker
      v-model="value1"
      type="date"
      placeholder="选择日期">
    </el-date-picker>
          </div></el-col>
          <el-col :span="8"> <div class="grid-content">
           <H3>带快捷选项</H3>
    <el-date-picker
      v-model="value2"
      align="right"
      type="date"
      placeholder="选择日期"
      :picker-options="pickerOptions">
    </el-date-picker>
          </div></el-col>
        </el-row>
      </div></div>
      <sTitle>
      <template #title>其他日期单位</template>
      <template #introduce>通过扩展基础的日期选择，可以选择周、月、年或多个日期</template>
    </sTitle>
    <div class="outbox">
      <div class="inbox">
        <el-row type="flex" justify="space-around">
          <el-col :span="8"> <div class="grid-content">
            <H3>周</H3>
            <el-date-picker
      v-model="value3"
      type="week"
      format="yyyy 第 WW 周"
      placeholder="选择周">
    </el-date-picker>
            </div></el-col>
          <el-col :span="8"> <div class="grid-content">
            <H3>选择多个日期</H3>
            <el-date-picker
      type="dates"
      v-model="value4"
      placeholder="选择一个或多个日期">
    </el-date-picker>
            </div></el-col>
        </el-row>
      </div></div>
      <sTitle>
      <template #title>其他日期单位</template>
      <template #introduce>通过扩展基础的日期选择，可以选择周、月、年或多个日期</template>
    </sTitle>
    <div class="outbox">
      <div class="inbox">
        <el-row type="flex" justify="space-around">
          <el-col :span="10"> <div class="grid-content">
            <H3>默认</H3>
            <el-date-picker
      v-model="value1"
      type="daterange"
      range-separator="至"
      start-placeholder="开始日期"
      end-placeholder="结束日期">
    </el-date-picker>
            </div></el-col>
          <el-col :span="10"> <div class="grid-content">
            <H3>带快捷选项</H3>
            <el-date-picker
      v-model="value2"
      type="daterange"
      align="right"
      unlink-panels
      range-separator="至"
      start-placeholder="开始日期"
      end-placeholder="结束日期"
      :picker-options="pickerOptions2">
    </el-date-picker>
            </div></el-col>
        </el-row>
      </div></div>
      <sTitle>
      <template #title>默认选择当年1号至今日日期</template>
      <template #introduce></template>
    </sTitle>
      <div class="outbox">
        <div class="inbox">
      <el-row>
        <el-col>
  <div class="block">
    <p>组件值：{{ value }}</p>
    <el-date-picker
    @change="logDate"
      v-model="value"
      type="daterange"
      start-placeholder="开始日期"
      end-placeholder="结束日期"
      :default-time="['00:00:00', '23:59:59']">
    </el-date-picker>
  </div>
        </el-col>
      </el-row>
        </div>
      </div>

  </div>
</template>

<script>

export default {
  name: 'ColorIndex',
  data () {
    return {
      pickerOptions2: {
        shortcuts: [{
          text: '最近一周',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '最近一个月',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '最近三个月',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
            picker.$emit('pick', [start, end])
          }
        }]
      },
      pickerOptions: {
        disabledDate (time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [{
          text: '今天',
          onClick (picker) {
            picker.$emit('pick', new Date())
          }
        }, {
          text: '昨天',
          onClick (picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24)
            picker.$emit('pick', date)
          }
        }, {
          text: '一周前',
          onClick (picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', date)
          }
        }]
      },
      value1: '',
      value2: '',
      value3: '',
      value4: '',
      value5: '',
      value6: '',
      value: ''
    }
  },
  methods: {
    logDate () {
      console.log(this.value)
    }
  },
  mounted () {
    setTimeout(() => {
      this.value = [new Date(new Date().getFullYear(), 0, 1), new Date()]
      console.log(this.value)
    }
    , 200)
  }

}
</script>

<style lang="less" scoped>
@import '../../styles/row.less';
.outbox{
    width: 100%;
    height: auto;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: center;
    .inbox{
      width: 85%;
      height: auto;
      background-color: #ffffff;
      border: 1px solid darkgray;
      .grid-content{
        height: auto;
        background: white;
        margin-bottom: 20px;
        align-items: center;
        }
    }}
</style>
